/** -----------------------
 * ワンポットパスタの特長
 * ---------------------- */
.feature_area {
	position: relative;
	background-color: #f9f7e5;
	&:before {
	@include desktop {
			content: "";
			display: block;
			position: absolute;
			top: -83px;
			width: 100%;
			height: 83px;
			background: url(#{$imgPath}feature_bg_top.png) repeat-x center top;
		}
	}
	&:after {
		@include desktop {
			content: "";
			display: block;
			position: absolute;
			bottom: -83px;
			width: 100%;
			height: 83px;
			background: url(#{$imgPath}feature_bg_bottom.png) repeat-x center top;
		}
	}
	.feature_contents {
		@extend %ext_base_block;
		.feature_ttl {
			text-align: center;
			@include desktop {
				margin-bottom: 26px;
				padding-top: 37px;
			}
		}
		.feature_lead {
			text-align: center;
				@include desktop {
				margin-bottom: 40px;
				font-size: 18px;
				line-height: 1.55em;
			}
		}
		.feature_list {
			display: flex;
			justify-content: space-between;
			li {
				@include desktop {
					width: 316px;
				}
				.feature_num1 {
					@include desktop {
						@include backgroundFig(316px, 40px, "feature_num1.png");
					}
				}
				.feature_num2 {
					@include desktop {
						@include backgroundFig(316px, 40px, "feature_num2.png");
					}
				}
				.feature_num3 {
					@include desktop {
						@include backgroundFig(316px, 40px, "feature_num3.png");
					}
				}
				.feature_inner {
					margin: 0 auto;
					@include desktop {
						width: 276px;
					}
					.feature_sub_ttl {
						text-align: center;
						@include desktop {
							margin: 22px 0 11px;
							font-size: 22px;
							line-height: 1.54em;
						}
					}
					.feature1_sub_ttl {
						@include desktop {
							margin: 36px 0 30px;
						}
					}
					.feature_desc {
						@include desktop {
							margin-bottom: 15px;
							font-size: 16px;
							line-height: 1.75em;
						}
					}
					.feature_notes {
						color: $font-red;
						@include desktop {
							font-size: 14px;
							line-height: 1.57em;
						}
					}
				}
			}
		}
	}
}


/** -----------
 * フッター
 * ----------- */
footer {
	background-color: #fff;
	.footer_contens {
		@extend %ext_base_block;
		@include desktop {
			margin: 0 auto;
			padding: 20px 0;
		}
		@include smartphone {
			padding: 20px 0;
		}
		.copyright {
			text-align: center;
			@include desktop {
				font-family: "Quicksand", "游ゴシック", "Yu Gothic", YuGothic, "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, sans-serif;
				font-size: 10px;
				line-height: 1em;
			}
			@include smartphone {
				width: 100%;
				font-size: 8px;
			}
		}
	}
}
